import { profileAPI } from '../api/api';
const ADD_POST = 'ADD-POST';
const DELETE_POST = 'DELETE_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';

const initialState = {
  postsData: [
    {
      id: 1,
      likes: '15 ',
      message: 'next, thank u',
      ava: 'http://data.whicdn.com/images/286297143/original.jpg',
    },
    {
      id: 2,
      likes: '42 ',
      message: 'i bought a 7 rings',
      ava: 'https://i.pinimg.com/originals/ac/2c/f6/ac2cf6654b878650b19fb18d8d5e46af.jpg',
    },
    {
      id: 3,
      likes: '12 ',
      message: 'Она любит деньги и я тоже Liga la sosa — она хочет больше',
      ava: 'https://i.pinimg.com/originals/ac/2c/f6/ac2cf6654b878650b19fb18d8d5e46af.jpg',
    },
  ],

  profile: null,
  status: '',
};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      let newPost = {
        id: 5,
        message: action.newPostText,
        likes: '0',
        ava: 'https://i.pinimg.com/originals/ac/2c/f6/ac2cf6654b878650b19fb18d8d5e46af.jpg',
      };
      let stateCopy = { ...state };

      stateCopy.postsData = [...state.postsData];
      //lesson 47
      stateCopy.postsData.push(newPost);
      stateCopy.newPostText = '';
      return stateCopy;
    }

    case SET_STATUS: {
      return { ...state, status: action.status };
    }
    case SET_USER_PROFILE: {
      return { ...state, profile: action.profile };
    }
    case DELETE_POST: {
      return {
        ...state,
        postsData: state.postsData.filter((p) => p.id !== action.id),
      };
    }
    default:
      return state;
  }
};

export const addPostActionCreator = (newPostText) => {
  return {
    type: ADD_POST,
    newPostText,
  };
};
export const setUserProfile = (profile) => {
  return {
    type: SET_USER_PROFILE,
    profile,
  };
};
export const setStatus = (status) => {
  return {
    type: SET_STATUS,
    status,
  };
};
export const deletePost = (id) => {
  return {
    type: DELETE_POST,
    id,
  };
};

export const getUserProfile = (userId) => async (dispatch) => {
  let response = await profileAPI.getProfile(userId);
  dispatch(setUserProfile(response.data));
};
export const getStatus = (userId) => async (dispatch) => {
  let response = await profileAPI.getStatus(userId);
  dispatch(setStatus(response.data));
};
export const updateStatus = (status) => async (dispatch) => {
  let response = await profileAPI.updateStatus(status);
  if (response.data.resultCode === 0) {
    dispatch(setStatus(status));
  }
};

export default profileReducer;
