import { NavLink } from 'react-router-dom';
import './Header.css';
const Header = (props) => {
  return (
    <header className='header'>
      <img
        src='https://i.pinimg.com/originals/5b/d3/08/5bd30877dcbc6a15e991fbe1a0f3acfd.png'
        width='60'
        height='60'
        alt='logo'
      ></img>
      <div className='login-block'>
        {props.isAuth ? (
          <div>
            {props.login} | <button onClick={props.logout}>Logout</button>
          </div>
        ) : (
          <NavLink className='hreef' to={'/login'}>
            Login
          </NavLink>
        )}
      </div>
      {/* <div>Sociality</div> */}
    </header>
  );
};
export default Header;
