import { NavLink } from "react-router-dom";
import "./Navbar.css";
const Navbar = ()=>{
    return(
        <nav className="nav">
        <div className="nav_item">
          <NavLink className='hreef' to="/profile">Profile</NavLink>
        </div>
        <div className="nav_item">
          <NavLink className='hreef' to="/dialogs">Messages</NavLink>
        </div >
        <div className="nav_item">
          <NavLink className='hreef' to="/news">News</NavLink>
        </div>
        <div className="nav_item">
          <NavLink className='hreef' to="/music">Music</NavLink>
        </div>
        <div className="nav_item">
          <NavLink className='hreef' to="/settings">Settings</NavLink>
        </div>
        <div className="nav_item">
          <NavLink className='hreef' to="/users">Users</NavLink>
        </div>
      </nav>
    )
}
export default Navbar