import "./Post.css";
const Post = (props) =>{
    
    return(
    <div>
     
        <span className="post_item">
          <img className='img_post' src='http://data.whicdn.com/images/286297143/original.jpg'width='55px' height='55px' alt="ava"/>
        
          {props.message}
        
        </span>
        
        <span className="like">{props.likes}Likes</span>
   
  </div>)
}
export default Post