const ADD_MESSAGE = 'ADD-MESSAGE';

let initialState = {
  messagesData: [
    { id: 1, message: 'hello buddy!!' },
    { id: 2, message: 'Wearing a ring, but aint gon be no Mrs.' },
    { id: 3, message: 'dont stop' },
    {
      id: 4,
      message: 'Когда мы первый раз увиделись, это было на концерте',
    },
    { id: 5, message: 'скуч по тебе.......' },
  ],
  dialogsData: [
    { id: 1, name: 'Darya' },
    { id: 2, name: 'Kirill' },
    { id: 3, name: 'Jhope' },
    { id: 4, name: 'Ariana' },
    { id: 5, name: 'Felix' },
  ],
};

const dialogsReducer = (state = initialState, action) => {
  let stateCopy = {
    ...state,
    messagesData: [...state.messagesData],
  };

  if (action.type === ADD_MESSAGE) {
    let newMessage = {
      id: 6,
      message: action.newMessageText,
    };
    stateCopy.messagesData.push(newMessage);
  }
  return stateCopy;
};
export const addMessageActionCreator = (newMessageText) => {
  return {
    type: ADD_MESSAGE,
    newMessageText,
  };
};

export default dialogsReducer;
